import { Component, OnInit } from '@angular/core';
import { POCService } from "../poc.service";

@Component({
    selector: 'show-tree',
    templateUrl: './show-tree.component.html',
    styleUrls: ['./show-tree.component.css']
})
export class ShowTreeComponent {
    constructor(private pocService: POCService) {

    }

    data;

    showTreeMap() {
        console.log("Inside showTreeMap");
        this.pocService.showTree().subscribe(data => this.data = data,
            error => console.log(error));
        console.log(this.data);
    }
    ngOnInit() {

    }
}


